@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap');
body{
	/* background-image: radial-gradient(circle, #ffffff, #f1ebf5, #e8d6e8, #e4c1d5, #e0abbe); */
	background: url('../Images/bg-color.png');
	background-size: cover;
}
/* NavBar */
.navbar 
{
	background-color: transparent ;
}

.navbar-fixed-top.scrolled {
    background-color: #fff;
    transition: background-color 200ms linear;
  }
.navbar-btn
{
	border-radius: 0%;
	margin: 10px;
	background-color: #ffffff;
	font-size: 20;
	width: 120pt;
	height: 38pt;
}
.navbar-brand
{
	font-family: 'Abril Fatface', cursive;
	font-size: 40pt !important;
}
.second-nav
{
	background-color: transparent !important;
	color: #000000;
}
.first-menu
{
	display: none;
}
.second-menu
{
	padding-top: 10px;
	margin-top: 0%;
	background-color: transparent !important;
	/* color: #000000; */
	padding-left: 0;
	padding-right: 0;
}


.blog-container {
    background: #fff;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.2) 0 4px 2px -2px;
    font-family: "adelle-sans", sans-serif;
    font-weight: 100;
    margin: 48px auto;
    margin-top: 150px;
    width: 20rem;
  }
  .btn-con{
	border-radius: 0px;
	background-color: #AB72F3;
	color: #ffffff;
}
  @media screen and (min-width: 480px) {
    .blog-container {
      width: 28rem;
    }
  }
  @media screen and (min-width: 767px) {
    .blog-container {
      width: 40rem;
    }
  }
  @media screen and (min-width: 959px) {
    .blog-container {
      width: 50rem;
    }
  }
  
  .blog-container a {
    color: #4d4dff;
    text-decoration: none;
    transition: 0.25s ease;
  }
  .blog-container a:hover {
    border-color: #ff4d4d;
    color: #ff4d4d;
  }
  
  .blog-cover {
    background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/17779/yosemite-3.jpg");
    background-size: cover;
    border-radius: 5px 5px 0 0;
    height: 15rem;
    box-shadow: inset rgba(0, 0, 0, 0.2) 0 64px 64px 16px;
  }
  
  .blog-author,
  .blog-author--no-cover {
    margin: 0 auto;
    padding-top: 0.125rem;
    width: 80%;
  }
  
  .blog-author h3::before,
  .blog-author--no-cover h3::before {
    background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/17779/russ.jpeg");
    background-size: cover;
    border-radius: 50%;
    content: " ";
    display: inline-block;
    height: 32px;
    margin-right: 0.5rem;
    position: relative;
    top: 8px;
    width: 32px;
  }
  
  .blog-author h3 {
    color: #fff;
    font-weight: 100;
  }
  
  .blog-author--no-cover h3 {
    color: #999999;
    font-weight: 100;
  }
  
  .blog-body {
    margin: 0 auto;
    width: 80%;
  }
  
  .video-body {
    height: 100%;
    width: 100%;
  }
  
  .blog-title h1 a {
    color: #333;
    font-weight: 100;
  }
  
  .blog-title h1 a:hover {
    color: #333;
    font-weight: 100;
  }
  
  .blog-summary p {
    color: #4d4d4d;
  }
  
  .blog-tags ul {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style: none;
    padding-left: 0;
  }
  
  .blog-tags li + li {
    margin-left: 0.5rem;
  }
  
  .blog-tags a {
    border: 1px solid #999999;
    border-radius: 3px;
    color: #999999;
    font-size: 0.75rem;
    height: 1.5rem;
    line-height: 1.5rem;
    letter-spacing: 1px;
    padding: 0 0.5rem;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;
    width: 5rem;
  }
  
  .blog-footer {
    border-top: 1px solid #e6e6e6;
    margin: 0 auto;
    padding-bottom: 0.125rem;
    width: 80%;
  }
  
  .blog-footer ul {
    list-style: none;
    display: flex;
    flex: row wrap;
    justify-content: flex-end;
    padding-left: 0;
  }
  
  .blog-footer li:first-child {
    margin-right: auto;
  }
  
  .blog-footer li + li {
    margin-left: 0.5rem;
  }
  
  .blog-footer li {
    color: #999999;
    font-size: 0.75rem;
    height: 1.5rem;
    letter-spacing: 1px;
    line-height: 1.5rem;
    text-align: center;
    text-transform: uppercase;
    position: relative;
    white-space: nowrap;
  }
  .blog-footer li a {
    color: #999999;
  }
  
  .comments {
    margin-right: 1rem;
  }
  
  .published-date {
    border: 1px solid #999999;
    border-radius: 3px;
    padding: 0 0.5rem;
  }
  
  .numero {
    position: relative;
    top: -0.5rem;
  }
  
  .icon-star,
  .icon-bubble {
    fill: #999999;
    height: 24px;
    margin-right: 0.5rem;
    transition: 0.25s ease;
    width: 24px;
  }
  .icon-star:hover,
  .icon-bubble:hover {
    fill: #ff4d4d;
  }

  /* // Small devices (landscape phones, 576px and up) */
@media (max-width: 576px) 
{
    .blog-container{
        margin-top: 100px;
        /* width: 90vw; */
    }
}
/* Form */
label {
    color: #333
}



.help-block.with-errors {
    color: #ff5050;
    margin-top: 5px
}




/* Footer */
.blockquote {
	margin-bottom: 1rem;
	font-size: 1.25rem; }
  
  .blockquote-footer {
	display: block;
	font-size: 80%;
	color: #6c757d; }
	.blockquote-footer::before {
	  content: "\2014\00A0"; }
  
  
  

  
  
  
  @media (min-width: 1200px) {
   
  }
  .img,
  .blog-img,
  .user-img {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center; }
  
  .ftco-footer-social li {
	list-style: none;
	margin: 0 10px 0 0;
	display: inline-block; }
	.ftco-footer-social li a {
	  height: 40px;
	  width: 40px;
	  display: block;
	  background: rgba(0, 0, 0, 0.05);
	  border-radius: 50%;
	  position: relative; }
	  .ftco-footer-social li a span {
		position: absolute;
		font-size: 20px;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		-ms-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%); }
	  .ftco-footer-social li a:hover {
		color: #fff; }
  
  .form-control {
	height: 52px;
	background: #fff;
	color: #000;
	font-size: 18px;
	border-radius: 0px;
	-webkit-box-shadow: none;
	box-shadow: none;
	border: 1px solid rgba(0, 0, 0, 0.1); }
	.form-control:focus, .form-control:active {
	  outline: none !important;
	  -webkit-box-shadow: none;
	  box-shadow: none; }
  
  textarea.form-control {
	height: inherit !important; }
  
  .ftco-section {
	padding: 12em 0; }
	.ftco-section h2 {
	  margin-bottom: 0; }
  
  footer {
	padding: 7em 0; }
  
  .footer-07 {
	background: #121212; }
	.footer-07 a {
	  color: #9b83de; }
	.footer-07 p {
	  color: rgba(255, 255, 255, 0.3); }
	.footer-07 .footer-heading {
	  font-size: 30px;
	  color: #fff;
	  font-weight: 700;
	  margin-bottom: 30px; }
	  .footer-07 .footer-heading .logo {
		color: #fff; }
	.footer-07 .menu {
	  margin-bottom: 30px; }
	  .footer-07 .menu a {
		color: rgba(255, 255, 255, 0.6);
		margin: 0 10px;
		text-transform: uppercase;
		letter-spacing: 1px; }
	.footer-07 .ftco-footer-social li a {
	  background: transparent;
	  border: 1px solid #9b83de; }
@media (max-width: 576px) {
	body{
		/* background-image: radial-gradient(circle, #ffffff, #f1ebf5, #e8d6e8, #e4c1d5, #e0abbe); */
		background: url('../Images/bg-color.png');
		background-size:contain;
	}
	.navbar
	{
		background-image: url('../Images/light-bg.png') !important;
	}
	.navbar-collapse
	{
		background-color: #ffffff !important;
	
	}
	.navbar-brand
	{
		font-family: 'Redressed', cursive;
		font-size: 20pt !important;
	}
}